<template>
  <el-header style="width: 100%">
    <img src="../assets/logo.jpg">
    <div class="user-info" v-if="userInfo">
      <div class="avatar"><span :style="`background-image:url(${userInfo.userAvatar})`"></span></div>
      <span> {{userInfo.userNickName}}</span>
    </div>
    <div class="login" v-else> <a v-on:click="go2LoginByWeibo">微博登录</a></div>
  </el-header>
</template>
<script>
export default {
  data() {
    return {
      userInfo: {}
    }
  },
  methods: {
    go2LoginByWeibo() {
      window.location.href = 'https://api.weibo.com/oauth2/authorize?client_id=4197904860&response_type=code&redirect_uri=http://www.chenguohua.com.cn'
    },
    loginByWeibo() {
      let code = window.location.href.match(/\?code=(\S*)#\//)
      let weiboLoginInfo = this.$utils.getCache({ key: 'weiboLoginInfo' })
      let loginInfo = this.$utils.getCache({ key: 'loginInfo' })
      if (code !== null) {
        code = code[1]
        this.$utils.ajax({
          url: '/api/login/getWeiboAccessToken',
          data: {
            code
          },
          success: res => {
            this.$utils.setCache({ key: 'weiboLoginInfo', value: res })
            window.location.href = 'http://www.chenguohua.com.cn/#/'
          }
        })
      } else if (weiboLoginInfo && !loginInfo) {
        this.$utils.ajax({
          url: '/api/login/loginByWeibo',
          data: {
            access_token: weiboLoginInfo.access_token,
            uid: weiboLoginInfo.uid
          },
          success: res => {
            if (res.status) {
              console.log(res)
              this.userInfo = res.data
              this.$utils.setCache({ key: 'loginInfo', value: res.data })
            } else {
              window.location.reload()
            }
          }
        })
      } else {
        this.userInfo = loginInfo
      }
    }
  },
  mounted() {
    this.loginByWeibo()
  }
}

</script>
<style lang="less" scoped="">
.el-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: @mainColor;

  img {
    width: 40px;
  }

  .login {
    font-size: 12px;
    color: @supportColor;

    a {
        cursor: pointer;
      text-decoration: underline;
    }
  }

  .user-info {
    display: flex;
    justify-content: space-between;
    align-items: center;

    >span {
      font-size: 12px;
      margin-left: 8px;
      color: #fff;
    }

    .avatar {
      width: 36px;
      height: 36px;

      span {
        display: block;
        width: 36px;
        height: 36px;
        background-size: cover;
        background-position: center;
        border-radius: 50%;
        background-color: #fff;
      }
    }
  }
}

</style>



