<template>
  <div>
    <section>
      <my-header></my-header>
      <el-main>
        <div style="width: 980px;margin: 0 auto;">
        <script id="editor" type="text/plain" ></script>
        </div>
        <el-row>
          <el-button v-on:click="doSubmit">提交</el-button>
        </el-row>
      </el-main>
    </section>
  </div>
</template>
<script>
import '../assets/UE/ueditor.config.js'
import '../assets/UE/ueditor.all.js'
import '../assets/UE/lang/zh-cn/zh-cn.js'
import '../assets/UE/ueditor.parse.js'
import myHeader from './header.vue'
export default {
  name: 'UE',
  components: {
    'my-header': myHeader
  },
  data() {
    return {
      editor: null
    }
  },
  props: {
    defaultMsg: {
      type: String
    },
    config: {
      type: Object
    }
  },
  mounted() {
    this.editor = UE.getEditor('editor', {}) // 初始化UE
  },
  methods: {
    getUEContent() { // 获取内容方法
      return this.editor.getContent()
    },
    doSubmit() {
      console.log(this.getUEContent())
      this.$utils.ajax({
        url: '/api/article/getCategoryList',
        data: {
          content: this.getUEContent(),
          userId: '1'
        }
      })
    }
  },
  destroyed() {
    this.editor.destroy()
  }
}

</script>
<style lang="less" scoped>
.el-container {
  height: 100%;

  >* {
    width: 100%;
  }
}

.el-row {
  margin-top: 10px;
}

</style>



